.login{
    background-color: white;
    height:100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.login-container{
    width: 300px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid lightgrey;
}

.login-container >h1{
    font-weight: 500;
    margin-bottom: 20px;
}
.login-container > form > h5{
    margin-bottom: 5px;
}

.login-container > form > input{
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}
.login-container > p{
    margin-top: 15px;
    font-size: 12px;
}

.login-container > form > button{
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
}

.login-container > button{
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgray;
}

.login-logo{
    width: 100px;
    object-fit: contain;
    margin-top: 20px;
    margin-bottom: 20px;
}